* {
  padding: 0;
  margin: 0;
}

body {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  background-color: #161618;
  margin: 0;
  overflow-x: hidden;
  font-family: "Manrope", sans-serif;
}
